export function ucFirst(chord) {
  chord = chord.toLowerCase();
  let uFirst = chord.charAt(0).toUpperCase();
  let cText = chord.slice(1);

  return uFirst + cText;
};

export function createArrNeck(neck, NUMBERLATIN, neckFull, neckType, neckUnit) {
  const defaultIndex = 2;
  const index = NUMBERLATIN.findIndex(item => item[0] === neck);
  const neckArr = [index - 2, index - 1, index, index + 1].map((item, idx) => {
    const avatar = () => {
      if (neckFull) {
        return NUMBERLATIN[item][neckType === 'latin' ? 0 : 1];
      } else {
        return idx !== defaultIndex ? '' : NUMBERLATIN[item][neckType === 'latin' ? 0 : 1];
      }
    }

    return avatar() + `${avatar() && neckType === 'number' ? neckUnit : ''}`
  });
  
  return neckArr;
}
