import CHORDDATA from './data/_index';
import NUMBERLATIN from './store/number-latin';
import { createArrNeck, ucFirst } from './utils';

class GuitarChordClass {
  constructor(ele, chord, opts) {
    this.chord = chord;
    this.ele = ele;
    this.defaultOptions = {
      // DIMENSIONS
      canvasWidth: 150,
      canvasHeight: 180,
      padding: 13,
      offsetLimitHeight: 55,
      lineWidth: 1,
      edgeHeight: 10,
      showHands: false,
      // COLOR
      backgroundColor: 'transparent',
      strokeColor: '#000000',
      // TITLE
      titleHeight: 24,
      titleBackground: '#FFFFFF',
      titleColor: '#1D1D1D',
      titleFont: '15px Arial',
      // NUT
      nutHeight: 5,
      // NOTE
      noteRadian: 9,
      noteBackground: '#000000',
      noteColor: '#FFFFFF',
      nodeFont: '12px Arial',
      // NECK
      neckType: 'latin', // latin or number format
      neckUnit: 'fr', // just use for number format
      neckFull: true,
      neckWidth: 20,
      neckColor: '#595959',
      neckFont: '10px Time',
      // USE
      useHeight: 25,
      useColor: '#666666',
      useFont: '13px Arial',
      // ARROW
      arrowColor: '#1D1D1D',
      arrowText: 'Thế tay',
      arrowFont: '13px Arial',
      arrowFontColor: '#1D1D1D',
      // ERROR
      errorChordFont: '20px Arial',
      errorChordColor: '#DD0000',
      errorTitle: 'Unknow chord !!!',
      errorTitleColor: '#DD0000',
      errorTitleFont: '17px Arial',
      errorDesc: "This chord plugin was not found\nin the data. Please double check\nyour chords or contact the\nplugin owner for\nmore information.",
      errorDescColor: '#999999',
      errorDescFont: '12px Arial',
    };
    this.copyOtions = {
      ...this.defaultOptions,
      ...opts,
    };
    this.calculatorOptions = {
      frameWidth: this.copyOtions.canvasWidth - (this.copyOtions.padding * 2) - this.copyOtions.neckWidth,
      frameHeight: this.copyOtions.canvasHeight - (this.copyOtions.titleHeight + this.copyOtions.offsetLimitHeight),
    };
    this.options = {
      ...this.copyOtions,
      ...this.calculatorOptions,
    };
    this.init();
  }
}
GuitarChordClass.prototype.init = function() {
  const { showHands } = this.options;
  const chordUppercase = ucFirst(this.chord);
  const chordData = this.getChordData(chordUppercase)

  this.chordData = chordData;
  this.chordDataLength = chordData.length;
  this.chordText = chordUppercase;
  this.chordIndex = 0;

  // CLEAR INNER TEXT
  this.clearText();

  // RENDER SINGLE OR MULTI
  switch (showHands) {
    case false: this.multiChord(); break;
    default: this.singleChord(); break;
  }
};
GuitarChordClass.prototype.multiChord = function() {
  // CREATE CANVAS
  this.canvas = document.createElement('canvas');
  this.canvas.style.userSelect = 'none';
  this.canvas.width = this.options.canvasWidth;
  this.canvas.height = this.options.canvasHeight;
  this.ctx = this.canvas.getContext('2d');
  this.ctx.textBaseline = 'top';

  // CHECK EXIST
  if (!this.chordData) {
    this.drawError();
    this.renderCanvas();
    return;
  }

  // RUNNER
  this.drawMultiChord();
  this.handleEvents();
  this.renderCanvas();
}
GuitarChordClass.prototype.singleChord = function () {
  // CLEAR INNER TEXT
  this.clearText();

  this.chordData.forEach(chord => {
    // CREATE CANVAS
    this.canvas = document.createElement('canvas');
    this.canvas.style.userSelect = 'none';
    this.canvas.width = this.options.canvasWidth;
    this.canvas.height = this.options.canvasHeight;
    this.ctx = this.canvas.getContext('2d');
    this.ctx.textBaseline = 'top';

    // CHECK EXIST
    if (!chord) {
      this.drawError();
      this.renderCanvas();
      return;
    }

    // RUNNER
    this.drawSingleChord(chord);
    this.renderCanvas();
  })
}
GuitarChordClass.prototype.getChordData = function(text) {
  const key = Object.keys(CHORDDATA).filter(item => {
    return item.slice(1).slice(0, -1).split('|').some(it => it === text);
  });

  return CHORDDATA[key];
};
GuitarChordClass.prototype.drawMultiChord = function () {
  const index = this.chordIndex;
  const { share, neck, use } = this.chordData[index];

  this.clearCanvas();
  this.drawFrame();
  this.drawTitle(this.chordText);
  this.drawNeck(neck);
  this.drawUse(use);
  this.drawFinger(share);
  this.drawArrow();
};
GuitarChordClass.prototype.drawSingleChord = function (chord) {
  const { share, neck, use } = chord;

  this.clearCanvas();
  this.drawFrame();
  this.drawTitle(this.chordText);
  this.drawNeck(neck);
  this.drawUse(use);
  this.drawFinger(share);
};
GuitarChordClass.prototype.clearCanvas = function() {
  const {
    canvasWidth,
    canvasHeight,
  } = this.options;

  this.ctx.beginPath();
  this.ctx.clearRect(0, 0, canvasWidth, canvasHeight);
}
GuitarChordClass.prototype.clearText = function() {
  this.ele.innerText = '';
};
GuitarChordClass.prototype.drawTitle = function(title) {
  const {
    padding,
    frameWidth,
    lineWidth,
    titleHeight,
    titleBackground,
    titleColor,
    titleFont,
  } = this.options;

  this.ctx.beginPath();
  this.ctx.rect(padding, 0, frameWidth + lineWidth, titleHeight);
  this.ctx.fillStyle = titleBackground;
  this.ctx.fill();
  this.ctx.font = titleFont;
  this.ctx.fillStyle = titleColor;

  const measureText = this.ctx.measureText(title)
  const textWidth = measureText.width;
  const textHeight = measureText.actualBoundingBoxAscent + measureText.actualBoundingBoxDescent;
  const xTextWidth = padding + (frameWidth / 2) - (textWidth / 2);

  this.ctx.fillText(title, xTextWidth, (titleHeight / 2) - (textHeight / 2));
};
GuitarChordClass.prototype.drawFrame = function() {
  var _ = this;
  const {
    canvasWidth,
    canvasHeight,
    lineWidth,
    padding,
    frameWidth,
    frameHeight,
    backgroundColor,
    strokeColor,
    edgeHeight,
    titleHeight,
    nutHeight,
  } = this.options;
  const beginX = titleHeight;

  // DRAW BACKGROUND
  this.ctx.beginPath();
  this.ctx.rect(0, 0, canvasWidth, canvasHeight);
  this.ctx.fillStyle = backgroundColor;
  this.ctx.fill();

  // DRAW NUT
  this.ctx.beginPath();
  this.ctx.rect(padding, beginX, frameWidth + lineWidth, nutHeight);
  this.ctx.fillStyle = strokeColor;
  this.ctx.fill();

  // DRAW VERTICAL LINE
  let start_ver = padding;
  let offset_ver = frameWidth / 5;

  for (var i = 0; i <= 5; i ++) {
    this.ctx.beginPath();
    this.ctx.rect(start_ver, beginX + nutHeight, lineWidth, frameHeight);
    this.ctx.fillStyle = strokeColor;
    this.ctx.fill();

    start_ver += offset_ver;
  }

  // DRAW HORIZONTAL LINE
  const limit_height = frameHeight - edgeHeight;
  let offset_hor = limit_height / 4; // tại đây cần tính toán lại theo chiều cao canvas
  let start_x = beginX + nutHeight + offset_hor;
  let count = 1;

  while (count <= 4) {
    this.ctx.beginPath();
    this.ctx.rect(padding, start_x, frameWidth, lineWidth);
    this.ctx.fillStyle = strokeColor;
    this.ctx.fill();
    start_x += offset_hor;
    count++;
  }
};
GuitarChordClass.prototype.drawFinger = function(chord) {
  chord.map((e, i) => {
    e.push(i + 1);
    this.drawNote(e);
  });
};
GuitarChordClass.prototype.drawNote = function(arr) {
  const {
    frameWidth,
    frameHeight,
    edgeHeight,
    noteBackground,
    padding,
    noteColor,
    nodeFont,
    noteRadian,
    titleHeight,
    nutHeight,
  } = this.options;

  const limit_height = frameHeight - edgeHeight;
  const offset_x = frameWidth / 5;
  const offset_y = limit_height / 4;
  const start_y = titleHeight + nutHeight + (offset_y / 2);

  if (arr[0] > 0) {
    // NOTE
    const x_arc = padding + (offset_x * (arr[2] - 1));
    const y_arc = start_y + (offset_y * (arr[0] - 1));

    this.ctx.beginPath();
    this.ctx.arc(x_arc, y_arc, noteRadian, 0, Math.PI * 2);
    this.ctx.fillStyle = noteBackground;
    this.ctx.fill();

    // TEXT
    this.ctx.beginPath();
    this.ctx.font = nodeFont;
    this.ctx.fillStyle = noteColor;

    const measureText = this.ctx.measureText(arr[1]);
    const textWidth = measureText.width;
    const textheight = measureText.actualBoundingBoxAscent + measureText.actualBoundingBoxDescent;
    const x_text = padding + (offset_x * (arr[2] - 1)) - (textWidth / 2);
    const y_text = start_y + (offset_y * (arr[0] - 1)) - (textheight / 2);

    this.ctx.fillText(arr[1], x_text, y_text);
  }
};
GuitarChordClass.prototype.drawNeck = function(neck) {
  const {
    titleHeight,
    nutHeight,
    canvasWidth,
    frameHeight,
    neckType,
    neckUnit,
    neckColor,
    neckFont,
    neckFull,
    edgeHeight,
  } = this.options;

  this.ctx.beginPath();
  this.ctx.font = neckFont;
  this.ctx.fillStyle = neckColor;

  const arrNeck = createArrNeck(neck, NUMBERLATIN, neckFull, neckType, neckUnit);
  const limit_height = frameHeight - edgeHeight;
  const step = (limit_height / 4);
  const measureText = this.ctx.measureText(arrNeck[2]);
  const textHeight = measureText.actualBoundingBoxAscent + measureText.actualBoundingBoxDescent;
  let y_text = (titleHeight + nutHeight + (step / 2) - (textHeight / 2));
  
  arrNeck.forEach(textNeck => {
    const textWidth = this.ctx.measureText(textNeck).width;
    const x_text = canvasWidth - textWidth;
  
    this.ctx.fillText(textNeck, x_text, y_text);
    y_text += step;
  })
};
GuitarChordClass.prototype.drawUse = function(use) {
  if (!use) return;

  const {
    frameWidth,
    frameHeight,
    useColor,
    useFont,
    padding,
    titleHeight,
  } = this.options;
  let start_ver = padding;
  let offset_ver = (frameWidth / 5);
  
  for (var i = 0; i <= 5; i ++) {
    if (use[i] === '') {
      start_ver += offset_ver;
      continue;
    }

    const text = use[i];

    this.ctx.beginPath();
    this.ctx.font = useFont;
    this.ctx.fillStyle = useColor;

    const textWidth = this.ctx.measureText(text).width;
    const start_x = start_ver - (textWidth / 2);
    const start_y = titleHeight + frameHeight + 5;

    this.ctx.fillText(text, start_x, start_y);
    start_ver += offset_ver;
  }
};
GuitarChordClass.prototype.drawArrow = function() {
  const {
    canvasHeight,
    frameWidth,
    padding,
    arrowText,
    arrowColor,
    arrowFont,
    arrowFontColor,
  } = this.options;
  const length = this.chordDataLength;
  const YFirst = canvasHeight - 12;
  const YSecond = YFirst - 7;
  const YThird = YFirst + 7;
  
  if (length === 1) return;
  
  // PREV
  const prevXFirst = padding - 5;
  const prevXSecond = prevXFirst + 7;

  this.ctx.beginPath();
  this.ctx.moveTo(prevXFirst, YFirst);
  this.ctx.lineTo(prevXSecond, YSecond);
  this.ctx.lineTo(prevXSecond, YThird);
  this.ctx.fillStyle = arrowColor;
  this.ctx.fill();
  this.prevArea = [prevXFirst, YSecond, prevXSecond, YThird];

  // NEXT
  const nextXFirst = frameWidth + padding + 5;
  const nextXSecond = nextXFirst - 7;

  this.ctx.beginPath();
  this.ctx.moveTo(nextXFirst, YFirst);
  this.ctx.lineTo(nextXSecond, YSecond);
  this.ctx.lineTo(nextXSecond, YThird);
  this.ctx.fillStyle = arrowColor;
  this.ctx.fill();
  this.nextArea = [nextXSecond, YSecond, nextXFirst, YThird];

  // TEXT
  this.ctx.beginPath();
  this.ctx.font = arrowFont;
  this.ctx.fillStyle = arrowFontColor;

  const textWidth = this.ctx.measureText(arrowText).width;
  const xTextWidth = padding + (frameWidth / 2) - (textWidth / 2);

  this.ctx.fillText(arrowText, xTextWidth, YFirst - 5);
};
GuitarChordClass.prototype.drawError = function() {
  const {
    canvasWidth,
    errorChordFont,
    errorChordColor,
    errorTitle,
    errorTitleColor,
    errorTitleFont,
    errorDesc,
    errorDescColor,
    errorDescFont,
  } = this.options;

  // WRITE CHORD
  this.ctx.beginPath();
  this.ctx.font = errorChordFont;
  this.ctx.fillStyle = errorChordColor;

  const textChord = `<${this.chordText}> haaaa...`
  const measureTextChord = this.ctx.measureText(textChord);
  const x_chord = (canvasWidth / 2) - (measureTextChord.width / 2);
  const y_chord = 20;
  this.ctx.fillText(textChord, x_chord, y_chord);

  // WRITE TITLE
  this.ctx.beginPath();
  this.ctx.font = errorTitleFont;
  this.ctx.fillStyle = errorTitleColor;

  const measureTextTitle = this.ctx.measureText(errorTitle);
  const x_title = (canvasWidth / 2) - (measureTextTitle.width / 2);
  const y_title = y_chord + 40;
  this.ctx.fillText(errorTitle, x_title, y_title);

  // WRITE DESC
  let y_desc = y_title + 20;
  let y_offset = 18;
  const descSplitText = errorDesc.split('\n');

  descSplitText.forEach((text) => {
    this.ctx.beginPath();
    this.ctx.font = errorDescFont;
    this.ctx.fillStyle = errorDescColor;
  
    const measureTextDesc = this.ctx.measureText(text);
    const x_desc = (canvasWidth / 2) - (measureTextDesc.width / 2);
    this.ctx.fillText(text, x_desc, y_desc);

    y_desc += y_offset;
  })
}
GuitarChordClass.prototype.handleEvents = function() {
  this.canvas.addEventListener('click', this.handleEventClickArrow.bind(this));
  this.canvas.addEventListener('mousemove', this.handleEventMouseMove.bind(this));
};
GuitarChordClass.prototype.handleEventClickArrow = function(e) {
  const { clientX, clientY } = e;
  const { canvasWidth, canvasHeight } = this.options;
  const { top, left } = this.canvas.getBoundingClientRect();
  const x = clientX - left;
  const y = clientY - top;
  const clientWidth = this.canvas.clientWidth;
  const clientHeight = this.canvas.clientHeight;
  const ratio_x = clientWidth / canvasWidth;
  const ratio_y = clientHeight / canvasHeight;
  const preAreaRatio = this.prevArea.map((item, index) => item * (index <= 1 ? ratio_x : ratio_y));
  const nextAreaRatio = this.nextArea.map((item, index) => item * (index <= 1 ? ratio_x : ratio_y));

  // HANDLE PREV
  if (
    preAreaRatio[0] <= x && x <= preAreaRatio[2] &&
    preAreaRatio[1] <= y && y <= preAreaRatio[3]
  ) {
    const currentIndex = this.chordIndex;
    const nextIndex = currentIndex - 1;
    const index = nextIndex <= 0 ? this.chordDataLength - 1 : nextIndex;
    
    this.chordIndex = index;
  }

  // HANDLE NEXT
  if (
    nextAreaRatio[0] <= x && x <= nextAreaRatio[2] &&
    nextAreaRatio[1] <= y && y <= nextAreaRatio[3]
  ) {
    const currentIndex = this.chordIndex;
    const nextIndex = currentIndex + 1;
    const index = nextIndex > this.chordDataLength - 1 ? 0 : nextIndex;
    
    this.chordIndex = index;
  }

  this.drawMultiChord();
};
GuitarChordClass.prototype.handleEventMouseMove = function(e) {
  const { clientX, clientY } = e;
  const { canvasWidth, canvasHeight } = this.options;
  const { top, left } = this.canvas.getBoundingClientRect();
  const x = clientX - left;
  const y = clientY - top;
  const clientWidth = this.canvas.clientWidth;
  const clientHeight = this.canvas.clientHeight
  const ratio_x = clientWidth / canvasWidth;
  const ratio_y = clientHeight / canvasHeight;
  const preAreaRatio = this.prevArea.map((item, index) => item * (index <= 1 ? ratio_x : ratio_y));
  const nextAreaRatio = this.nextArea.map((item, index) => item * (index <= 1 ? ratio_x : ratio_y));

  if (
    (preAreaRatio[0] <= x && x <= preAreaRatio[2] &&
    preAreaRatio[1] <= y && y <= preAreaRatio[3]) ||
    (nextAreaRatio[0] <= x && x <= nextAreaRatio[2] &&
    nextAreaRatio[1] <= y && y <= nextAreaRatio[3])
  ) {
    this.canvas.style.cursor = 'pointer';
  } else {
    this.canvas.style.cursor = 'auto';
  }
};
GuitarChordClass.prototype.renderCanvas = function () {
  this.ele.appendChild(this.canvas);
};
/*===============================
========== BINDING EVENT ========
===============================*/
// BING EVENT
window.GuitarChord = {
  render: (ele, chord, opts) => {
    new GuitarChordClass(ele, chord, opts);
  },
  clear: (ele) => {
    ele.innerHTML = '';
  },
  info: {
    version: '1.1.2',
    publishDate: '23/02/2024',
    author: 'Hoang Minh Ngoc (minhngoc.ith)',
    email: 'minhngoc.ith@gmail.com'
  }
};

// AUTO RENDER CHORD WITH DATA ATTRIBUTES
window.addEventListener('DOMContentLoaded', () => {
  document.querySelectorAll('[data-guitar-chord]')
    .forEach(ele => {
      const chord = ele.getAttribute('data-guitar-chord') || ele.innerText;
      const showHands = ele.hasAttribute('data-show-hands');

      new GuitarChordClass(ele, chord, { showHands });
  });
});